<template>
  <el-row :gutter="24" class="content">
    <el-col :span="24" class="formBlock">
      <div class="formBlockContentFather">
        <div class="formBlockContent">
          <el-row :gutter="24">
            <el-col :span="24" class="headBlock">
              <span>欢迎注册</span>
            </el-col>
          </el-row>
          <el-row :gutter="24">
            <el-steps :active="showPage" align-center>
              <el-step title="填写资料"></el-step>
              <el-step title="注册审核"></el-step>
            </el-steps>
          </el-row>
          <el-row :gutter="24" class="formContent">
            <el-col :span="20" class="formContentCol">
              <el-form
                ref="form"
                :rules="rules"
                :hide-required-asterisk="true"
                :model="form"
                label-width="0px"
              >
                <div v-if="showPage === GLOBAL.VARIABLE.ONE_NUMBER">
                  <el-form-item prop="companyName">
                    <el-input v-model="form.companyName" placeholder="请输入企业名称（必填）"></el-input>
                  </el-form-item>
                 <el-form-item prop="telephoneNumber">
                    <el-input v-model="form.telephoneNumber" placeholder="请输入手机号"></el-input>
                  </el-form-item>
                  <el-form-item prop="code" >
                    <el-input
                      v-model="form.code"
                      placeholder="请输入验证码"
                      class="input-with-select"
                    >
                      <el-button
                        slot="append"
                        type="button"
                        @click="getCode"
                        :disabled="!canClick"
                      >{{codeBtn}}</el-button>
                    </el-input>
                  </el-form-item>
                  <el-form-item prop="userName">
                    <el-input v-model="form.userName" placeholder="请输入用户名（必填）"></el-input>
                  </el-form-item>
                  <el-form-item prop="realName">
                    <el-input v-model="form.realName" placeholder="请输入姓名"></el-input>
                  </el-form-item>
                  <el-form-item prop="email">
                    <el-input v-model="form.email" placeholder="请输入邮箱"></el-input>
                  </el-form-item>
                  <el-form-item prop="password">
                    <label slot="label"></label>
                    <el-input v-model="form.password" type="password" placeholder="请输入密码（必填）"></el-input>
                  </el-form-item>
                  <el-form-item prop="confirmPassword">
                    <label slot="label"></label>
                    <el-input
                      v-model="form.confirmPassword"
                      type="password"
                      placeholder="请再次输入密码（必填）"
                    ></el-input>
                  </el-form-item>
                </div>
                <div v-if="showPage===GLOBAL.VARIABLE.TWO_NUMBER" class="auditContent">
                  <i class="el-icon-success"></i>
                  <p>感谢您的注册，我们会第一时间对您申请的信息进行审核。请注意查收短信及邮箱信息！如需提供帮助，请您拨打免费服务热线：0574-27852098</p>
                </div>
              </el-form>
            </el-col>
            <el-col :span="20" class="formContentCol">
              <el-button
                type="primary"
                class="buttonFull"
                v-if="showPage===GLOBAL.VARIABLE.ONE_NUMBER"
                @click="submit"
              >提交注册</el-button>
              <el-button
                type="primary"
                class="buttonFull"
                v-if="showPage===GLOBAL.VARIABLE.TWO_NUMBER"
              >返回网址首页</el-button>
            </el-col>
            <el-col :span="20" class="formContentCol linkBlock">
              <span>已有账号?</span>
              <el-button type="text" @click="toHome">立即登录 ></el-button>
            </el-col>
          </el-row>
        </div>
      </div>
    </el-col>
  </el-row>
</template>

<script>
// 导入所需接口
import {
  userCheckUserNameRepeatAction,
  companyRegisterCompanyAction,
  userPhoneLoginRegisterSendCodeAction,
  userCheckIphoneRepeatAction
} from '@/api/management';
export default {
  components: {},
  data() {
    // 用于验证用户名是否重复
    var validateUserName = (rule, value, callback) => {
      if (!value) {
        callback(new Error('请输入用户名'));
      } else {
        userCheckUserNameRepeatAction({
          userName: value
        })
          .then(res => {
            // 请求数据正常
            if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
              if (res.rows) {
                callback();
              } else {
                callback(new Error('该用户名已被使用'));
              }
            }
          });
      }
    };
    // 用于验证密码是否输入正确
    var validatePass = (rule, value, callback) => {
      if (!value) {
        callback(new Error('请输入密码'));
      } else {
        if (this.form.confirmPassword !== '') {
          this.$refs.form.validateField('confirmPassword');
        }
        callback();
      }
    };
    // 用于验证重复密码是否输入正确
    var validatePassConfirm = (rule, value, callback) => {
      if (!value) {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.form.password) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    // // 用于验证验证码是否输入正确
    // var verificationCode = (rule, value, callback) => {
    //   if (!value) {
    //     callback(new Error('请输入验证码'));
    //   } else if (value !== this.verificationCode + '') {
    //     callback(new Error('请输入正确的验证码!'));
    //   } else {
    //     callback();
    //   }
    // };
    // 用于验证验证码是否输入正确
    var valiPhone = (rule, value, callback) => {
      var reg = /^1[0-9]{10}$/;
      if (value) {
        if (value.length <= 10 || !reg.test(value)) {
          callback(new Error('请输入正确的手机号码!'));
        } else {
          userCheckIphoneRepeatAction({
            telephoneNumber: value
          })
            .then(res => {
              // 请求数据正常
              if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
                if (res.rows) {
                  callback();
                } else {
                  callback(new Error('该手机号已被注册'));
                }
              }
            });
        }
      } else {
        callback();
      }
    };
    var valiCode = (rule, value, callback) => {
      if (this.form.telephoneNumber && this.canClick) {
        if (!value) {
          callback(new Error('请输入验证码'));
        } else {
          callback();
        }
      } else {
        callback();
      }
    };
    return {
      // 能否点击
      codeBtn: '获取验证码',
      setIn: '',
      canClick: true,
      time: 59,
      // 控制显示组件 1：填写资料 2：注册审核
      showPage: 1,
      // 表单数据
      form: {},
      // 验证码
      // verificationCode: 0,
      // 表单验证配置
      rules: {
        companyName: [
          {
            required: true,
            message: '请输入企业名称',
            trigger: ['blur']
          }
        ],
        telephoneNumber: [
          {
            validator: valiPhone,
            // required: true,
            trigger: ['blur']
          }
        ],
        userName: [
          {
            validator: validateUserName,
            required: true,
            trigger: ['blur']
          }
        ],
        realName: [
          {
            // required: true,
            message: '请输入姓名',
            trigger: ['blur']
          }
        ],
        code: [
          {
            validator: valiCode,
            // required: true,
            message: '验证码',
            trigger: ['blur']
          }
        ],
        email: [
          {
            // required: true,
            type: 'email',
            message: '请输入正确的邮箱',
            trigger: ['blur']
          }
        ],
        password: [
          {
            validator: validatePass,
            required: true,
            trigger: ['blur']
          }
        ],
        confirmPassword: [
          {
            validator: validatePassConfirm,
            required: true,
            trigger: ['blur']
          }
        ]
        // verificationCode: [
        //   {
        //     validator: verificationCode,
        //     required: true,
        //     trigger: ['blur']
        //   }
        // ]
      }
    };
  },
  watch: {},
  computed: {},
  created: function() {},
  methods: {
    getCode() {
      this.$refs.form.validateField('telephoneNumber', valid => {
        if (!valid) {
          userPhoneLoginRegisterSendCodeAction({
            phoneNumber: this.form.telephoneNumber
          })
            .then(res => {
              // 请求数据正常
              if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
                this.canClick = false;
                this.codeBtn = (this.time--) + '秒';
                this.toTime();
                // 操作成功提示
                this.$message({
                  showClose: true,
                  message: res.message,
                  type: 'success'
                });
              } else {
                // 操作异常提示
                this.$message({
                  showClose: true,
                  message: res.message,
                  type: 'warning'
                });
              }
            });
        }
      });
    },
    toHome() {
      this.$router.push('/login');
    },
    toTime() {
      const _that = this;
      setTimeout(function() {
        if (_that.time === 0) {
          _that.codeBtn = '获取验证码';
          _that.canClick = true;
          _that.time = 59;
        } else {
          var num = _that.time--;
          if (num < 10) {
            _that.codeBtn = '0' + num + '秒';
          } else {
            _that.codeBtn = num + '秒';
          }
          _that.toTime();
        }
      }, 1000);
    },
    // /**
    //  * @description: 执行下一步
    //  * @param 无
    //  * @return: 无
    //  * @author: ZJM
    //  */
    // next() {
    //   // 验证表单内容
    //   var _that = this;
    //   this.$refs.form.validate(valid => {
    //     if (valid) {
    //       // 验证成功
    //       // 显示用户部分组件
    //       this.showPage = this.GLOBAL.VARIABLE.TWO_NUMBER;
    //       // 用于限制表单验证
    //       this.passDO = this.GLOBAL.VARIABLE.ONE_NUMBER;
    //       // 清空限制验证
    //       setTimeout(function() {
    //         _that.passDO = _that.GLOBAL.VARIABLE.ZERO_NUMBER;
    //       }, _that.GLOBAL.VARIABLE.ZERO_NUMBER);
    //     } else {
    //       return false;
    //     }
    //   });
    // },
    // /**
    //  * @description: 清空表单数据和验证
    //  * @param 无
    //  * @return: 无
    //  * @author: ZJM
    //  */
    // reset() {
    //   // 清空表单数据和验证
    //   this.showPage = this.GLOBAL.VARIABLE.ONE_NUMBER;
    //   // 用于限制表单验证
    //   // this.passDO = this.GLOBAL.VARIABLE.ONE_NUMBER;
    //   // 清空限制验证
    //   var _that = this;
    //   setTimeout(function() {
    //     _that.passDO = _that.GLOBAL.VARIABLE.ZERO_NUMBER;
    //   }, _that.GLOBAL.VARIABLE.ZERO_NUMBER);
    //   // 清空表单对象数据
    //   this.form = {};
    //   this.$refs.form.clearValidate();
    // },
    /**
     * @description: 点击提交验证
     * @param 无
     * @return: 无
     * @author: ZJM
     */
    submit() {
      // 验证表单内容
      this.$refs.form.validate(valid => {
        if (valid) {
          // 执行接口保存
          this.submitData();
        } else {
          return false;
        }
      });
    },
    submitData() {
      if (this.form.telephoneNumber === '') {
        delete this.form.telephoneNumber;
        delete this.form.code;
      }
      companyRegisterCompanyAction(this.form)
        .then(res => {
          // 请求数据正常
          if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
            // 调用清空数据方法
            // this.reset();
            this.showPage = this.GLOBAL.VARIABLE.TWO_NUMBER;
            // 操作成功提示
            this.$message({
              showClose: true,
              message: res.message,
              type: 'success'
            });
          } else {
            // 操作异常提示
            this.$message({
              showClose: true,
              message: res.message,
              type: 'warning'
            });
          }
        });
    }
  }
};
</script>

